html {
  box-sizing: border-box;
}
body {
  padding: 20px;
  background: #2c3e50;
}
.filter-list {
  margin: auto;
  width: 300px;
  background: #3498db;
  border-radius: 5px;
  border: 1px solid #217bb8;
}
.filter-list input {
  width: 100%;
  display: block;
  padding: 10px;
  border-radius: 5px 5px 0px 0px;
  border: 0;
  font-size: 24px;
}
.filter-list input:focus {
  outline: none;
}
.filter-list ul {
  margin: 0;
  padding: 0;
}
.filter-list ul li {
  list-style-type: none;
  margin: 0;
  color: #fff;
  padding: 10px 20px;
  border-top: 1px solid #217bb8;
}
.filter-list ul li:hover {
  background: #2980b9;
}
.counter {
  width: 300px;
  margin: auto;
  background: #9b59b6;
  color: #fff;
  padding: 20px;
  text-align: center;
}
.counter h1 {
  margin: 0;
  padding: 20px;
  font-size: 36px;
}
.counter button {
  background: #f1c40f;
  border: 0;
  box-shadow: 0px 5px 0px #c29d0b;
  padding: 20px;
  width: 100%;
  outline: none;
  border-radius: 3px;
  color: #803d9c;
  font-weight: bold;
}
